.header {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  height: 50px;
}

a {
  border: 0px;
  margin: 0px;
  line-height: 50px;
  color: #6599ed;
  font-weight: bold;
  text-transform: capitalize
}

a:active, a:hover {
  color: white;
  font-weight: bold
}

li:hover {
  background: #6599ed;
  border: 0px;
  padding-bottom: 2px;
}


nav {
  width: 100%;
}

ul {
  margin: 0px;
  padding: 0px;
  height: 100%;
}

li {
  list-style: none;
  display: inline-block;
  width: 50%;
  height: 100%;
  text-align: center;
  background: white;
  border-bottom: 2px solid #6599ed;
}


